<template>
    <div class="edit-panel">
        <div class="edit-tips">
            <Icon type="md-options" />当前选中：
            <strong>背景</strong>
        </div>
        <options-bg :params="params" v-if="isBg"></options-bg>
        <options-default
            :wrapStyle="wrapStyle"
            :nodeStyle="nodeStyle"
            v-if="!isBg"
            @changePropsStyleValue="changePropsStyleValue"
        ></options-default>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
    /**
     * 编辑面板
     * 1. 根据选中的元素，切换适合的编辑面板
     * 2. 页面初始化时默认加载 options-bg 编辑面板
     */
    name: "edit-panel",
    data() {
        return {
            inheritStyle: {},
            isBg: false
        };
    },
    computed: {
        ...mapState({
            page: state => state.page.pageInfo,
            actUid: state => state.page.actPluginUid
        })
    },
    watch: {
        actUid: {
            handler(val) {
                /**
                 * 1. 监听正在编辑的元素
                 * 2. 如果actuid = false
                 *      2.1 则显示背景编辑面板
                 * 3.  如果actuid = true
                 *      3.1 获取正在编辑的元素的独有的编辑面板 eleEdit
                 *      3.2 然后显示默认编辑面板 + eleEdit（插槽方式引入）
                 */
                if (!val) {
                    this.isBg = true;
                    this.params = this.page.style;
                } else {
                    this.isBg = false;
                    this.page.plugins.map(it => {
                        if (it.uid == val) {
                            this.wrapStyle = it.wrapStyle;
                            this.nodeStyle = it.nodeStyle;
                            this.$forceUpdate();
                            return;
                        }
                    });
                }
            },
            deep: true,
            immediate: true
        }
    },
    components: {
        "options-bg": () => import("../edit-options/options-bg/options-bg"),
        "options-default": () =>
            import("../edit-options/options-default/options-default")
    },
    methods: {
        /**
         * 改变样式中的属性
         */
        colorChange(obj, propObj) {
            if (propObj) {
                Object.assign(this[obj], propObj);
            }
        },
        changePropsStyleValue(trager, obj) {
            if (obj) {
                Object.assign(this[trager], obj);
            }
        }
    }
};
</script>

<style lang="less" scoped>
@import url("./style.less");
</style>
